	module.exports = {"data":{"markdownRemark":{"html":"<p>Cuốn này thực ra khá hữu ích cho mình khi biết được quá trình làm việc của 1 designer từ bước lên ý tưởng, tìm tòi phân tích ý tưởng cho đến thiết kế. Một số công cụ khá hay được tác giả giới thiệu và mình biết thêm được khá khá các nguyên tắc trong thiết kế. (như áp dụng tỉ lệ vàng hay ý nghĩa màu sắc chẳng hạn).</p>\n<p>Cơ mà cuốn này có 2 nhược điểm:</p>\n<ol>\n<li>\n<p>Rất nhiều các trang mà tác giả đề cập giờ đã chết. Có thể bản mình dùng là 2014 nên đến 2018 mọi thứ chưa được cập nhật lắm.</p>\n</li>\n<li>\n<p>Cuốn sách này không được tương tác lắm. Có thể vì mình đang theo luồng học theo tutorial các video dạy các thứ rồi thì đọc cuốn \"Head first: Javascript\" đều có bài tập tương tác (gõ vào console, gõ luôn trên IDE các kiểu).</p>\n</li>\n</ol>\n<p>Vì thế mà mình quyết định sẽ thôi không blog về cuốn này nữa mà cuối cùng chỉ chốt lại 1 cái review dựa trên những thứ mình đọc được, thế thôi.</p>","frontmatter":{"date":"April 01, 2018","path":"/ve-cuon-principle-of-beautiful-web-design","title":"Head first: JavaScript Chapter 4"}}},"pathContext":{}}

